}
.goods__checkbox-indicator:checked + label::after {
  position: absolute;
  content: '';
  height: 34px;
  width: 19px;
  top: -11px;
  left: -18px;
  background-image: url('../img/iconfinder_Check_1063906.svg');
  background-repeat: repeat;
  background-position: center;
}
.goods__checkbox-pointer {
  display: block;
